import React, { useState } from 'react';
import styles from './MoreInfoFormSection.module.css';
import NextStepButton from '../UI/NextStepButton';
import PreviousStepButton from '../UI/PreviousStepButton';
import { isName, isValidSSN } from '../../validators';

function isValidGraduationDate(date) {
  const pattern = /^\d{4}-(0[1-9]|1[0-2])-((0[1-9]|1\d|2[0-9])|3[0-1])$/;

  const currentDate = new Date();
  const inputDate = new Date(date);

  const isValidDate = pattern.test(date) && inputDate >= currentDate;

  return isValidDate;
}

//   // ...
  
//   const enteredGraduationDateIsNotValid = !isValidGraduationDate(enteredGraduationDate);
  

const MoreInfoFormSection = (props) => {

  const { values } = props;
  
 
  const [enteredGPA, setEnteredGPA] = useState(values.GPA || '');
  const [enteredGPATouched, setEnteredGPATouched] = useState(false);
  const [enteredGraduationDate, setEnteredGraduationDate] = useState(values.graduationDate || '');
  const [enteredGraduationDateTouched, setEnteredGraduationDateTouched] = useState(false);

  const currentDate = new Date(); // Get the current date

 
  const enteredGPAIsEmpty = enteredGPA.trim().length === 0;
  const enteredGPAIsNotValid = isNaN(parseFloat(enteredGPA)) || parseFloat(enteredGPA) < 0;
  const enteredGraduationDateIsEmpty = enteredGraduationDate.trim().length === 0;
  const enteredGraduationDateIsNotValid =
  !isValidGraduationDate(enteredGraduationDate) || enteredGraduationDate < currentDate;


  
  const emptyGPA = enteredGPAIsEmpty && enteredGPATouched;
  const invalidGPA = !enteredGPAIsEmpty && enteredGPAIsNotValid && enteredGPATouched;
  const emptyGraduationDate = enteredGraduationDateIsEmpty && enteredGraduationDateTouched;
  const invalidGraduationDate =
    !enteredGraduationDateIsEmpty &&
    enteredGraduationDateIsNotValid &&
    enteredGraduationDateTouched;

 

  const enteredGPAChangeHandler = (e) => {
    setEnteredGPA(e.target.value);
  };

  const enteredGPABlurHandler = () => {
    setEnteredGPATouched(true);
  };

  const enteredGraduationDateChangeHandler = (e) => {
    setEnteredGraduationDate(e.target.value);
  };

  const enteredGraduationDateBlurHandler = () => {
    setEnteredGraduationDateTouched(true);
  };

  const allInputsValid =
    !enteredGPAIsEmpty &&
    !enteredGPAIsNotValid &&
    !enteredGraduationDateIsEmpty &&
    !enteredGraduationDateIsNotValid;

  const continueToNextStep = (e) => {
    e.preventDefault();

    if (allInputsValid) {
      props.handleChange({
        GPA: enteredGPA,
        graduationDate: enteredGraduationDate,
        currentDate: currentDate,
      });
      props.nextStep();
    }
  };

  const returnToPreviousStep = (e) => {
    e.preventDefault();
    props.prevStep();
};

  return (
    <div className={styles.formSection}>
      <h1>More Info</h1>
      <p className={styles.description}>
        Please provide your GPA and expected graduation date.
      </p>
      
      <div className={styles.formElement}>
        <div className={styles.labelRow}>
          <label htmlFor='gpa'>GPA</label>
          {emptyGPA && <p className={styles.error}>GPA cannot be empty.</p>}
          {invalidGPA && <p className={styles.error}>Please enter a valid GPA.</p>}
        </div>
        <input
          className={
            emptyGPA || invalidGPA
              ? `${styles.yourInputInput} ${styles.inputError}`
              : `${styles.yourInputInput}`
          }
          type='number'
          id='gpa'
          placeholder='e.g. 3.8'
          onChange={enteredGPAChangeHandler}
          onBlur={enteredGPABlurHandler}
          value={enteredGPA}
        />
      </div>
      <div className={styles.formElement}>
        <div className={styles.labelRow}>
          <label htmlFor='graduation-date'>Expected Graduation Date</label>
          {emptyGraduationDate && (
            <p className={styles.error}>Expected graduation date cannot be empty.</p>
          )}
          {invalidGraduationDate && (
            <p className={styles.error}>Graduation date should be on or after today's date.</p>
          )}

        </div>
        <input
          className={
            emptyGraduationDate || invalidGraduationDate
              ? `${styles.yourInputInput} ${styles.inputError}`
              : `${styles.yourInputInput}`
          }
          type='date'
          id='graduation-date'
          onChange={enteredGraduationDateChangeHandler}
          onBlur={enteredGraduationDateBlurHandler}
          value={enteredGraduationDate}
        />
      </div>
      <div className={styles.buttonRow}>
      <PreviousStepButton onClick={returnToPreviousStep}>Go Back</PreviousStepButton>
        <NextStepButton onClick={continueToNextStep}>Next Step</NextStepButton>
      </div>
    </div>
  );
};

export default MoreInfoFormSection;
