const isName = (name) => {
    const re = /^[a-zA-Z -]+$/;
    return re.exec(name);
};

const isEmail = (email) => {
    let res = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    return res.test(email);
};

const isValidSSN = (ssn) => {
    const re = /^[0-9]{4}$/;
    return re.test(ssn);
  };

  const isValidAmountSeeking = (amount) => {
    const re = /^\d+(\.\d{1,2})?$/;
    return re.test(amount);
  };

  const isValidFICO = (score) => {
    const re = /^(?:[0-9]{3}|[0-9]{2}[a-zA-Z])$/;
    return re.test(score);
  };

export { isName };
export { isEmail };
export { isValidSSN };
export { isValidAmountSeeking };
export { isValidFICO };