import React, { useState } from 'react';
import styles from './FinancialFormSection.module.css';
import NextStepButton from '../UI/NextStepButton';
import PreviousStepButton from '../UI/PreviousStepButton';

// validators
import { isValidAmountSeeking, isValidFICO } from '../../validators';




const FinancialFormSection = (props) => {
  // Form input states
  const { values } = props;

  const educationLoanCompanies = [
    'Earnest',
    'LendKey',
    'MPower',
    'Sallie Mae',
  'Navient',
  'Discover Student Loans',
  'Wells Fargo',
  'Citizens Bank',
  'SoFi',
  'College Ave Student Loans',
  'SunTrust',
  'PNC Bank',
  'Ascent',
   'Other',
    // Add more education loan companies as needed
  ];

  const isValidCreditScore = (creditScore) => {
    // Convert the credit score to a number
    const numericScore = Number(creditScore);
  
    // Check if the numeric score is within the valid range
    return numericScore >= 300 && numericScore <= 850;
  };
  
  const [enteredAmountSeeking, setEnteredAmountSeeking] = useState(values.amountSeeking || '');
  const [enteredAmountSeekingTouched, setEnteredAmountSeekingTouched] = useState(false);
  const [enteredCurrentEducationLoans, setEnteredCurrentEducationLoans] = useState(values.currentEducationLoans || '');
  const [enteredCurrentEducationLoansTouched, setEnteredCurrentEducationLoansTouched] = useState(false);
  const [hasCosigner, setHasCosigner] = useState(false);
  const [enteredCreditScore, setEnteredCreditScore] = useState(values.creditScore || '');
  const [enteredCreditScoreTouched, setEnteredCreditScoreTouched] = useState(false);
  const [coSigner, setCoSigner] = useState(values.hascoSigner || '');
  const [coSignerTouched, setCoSignerTouched] = useState(false);


  // Input validators
  const enteredAmountSeekingIsEmpty = enteredAmountSeeking.trim().length === 0;
  const enteredAmountSeekingIsNotValid = !isValidAmountSeeking(enteredAmountSeeking);
  const enteredCurrentEducationLoansIsEmpty = enteredCurrentEducationLoans.trim().length === 0;
  const hasCosignerIsNotValid = !hasCosigner;
  const enteredCreditScoreIsEmpty = enteredCreditScore.trim().length === 0;
  const enteredCreditScoreIsNotValid = !isValidCreditScore(enteredCreditScore); // Add your credit score validation function
  

  // Validator message conditions
  const emptyAmountSeeking = enteredAmountSeekingIsEmpty && enteredAmountSeekingTouched;
  const invalidAmountSeeking = !enteredAmountSeekingIsEmpty && enteredAmountSeekingIsNotValid && enteredAmountSeekingTouched;
  const emptyCurrentEducationLoans = enteredCurrentEducationLoansIsEmpty && enteredCurrentEducationLoansTouched;
  const hasCosignerIsEmpty = coSigner.trim().length === 0;
  const invalidCosigner = hasCosignerIsEmpty;
  const emptyCreditScore = enteredCreditScoreIsEmpty && enteredCreditScoreTouched;
  const invalidCreditScore = !enteredCreditScoreIsEmpty && enteredCreditScoreIsNotValid && enteredCreditScoreTouched; 

  const enteredAmountSeekingChangeHandler = (e) => {
    setEnteredAmountSeeking(e.target.value);
  };

  const enteredAmountSeekingBlurHandler = () => {
    setEnteredAmountSeekingTouched(true);
  };

  const enteredCurrentEducationLoansChangeHandler = (e) => {
    setEnteredCurrentEducationLoans(e.target.value);
  };

  const enteredCurrentEducationLoansBlurHandler = () => {
    setEnteredCurrentEducationLoansTouched(true);
  };

  const hasCosignerChangeHandler = (e) => {
    setHasCosigner(e.target.checked);
  };

  const enteredCreditScoreChangeHandler = (e) => {
    setEnteredCreditScore(e.target.value);
  };
  
  const enteredCreditScoreBlurHandler = () => {
    setEnteredCreditScoreTouched(true);
  };

  const coSignerChangeHandler = (e) => {
    setCoSigner(e.target.value);
  };

  const coSignerBlurHandler = () => {
    setCoSignerTouched(true);
  };

  const coSignerOptions = [
    { value: '', label: 'Do you have a co-signer?' },
    { value: 'Yes', label: 'Yes' },
    { value: 'No', label: 'No' },
  ];
  
  const coSignerIsEmpty = coSigner.trim().length === 0;

  const allInputsValid =
    !enteredAmountSeekingIsEmpty &&
    !enteredAmountSeekingIsNotValid &&
    !enteredCurrentEducationLoansIsEmpty &&
    !enteredCreditScoreIsEmpty &&
    !enteredCreditScoreIsNotValid &&
    !invalidCosigner;

  const continueToNextStep = (e) => {
    e.preventDefault();

    if (allInputsValid) {
      props.handleChange({
        amountSeeking: enteredAmountSeeking,
        currentEducationLoans: enteredCurrentEducationLoans,
        creditScore: enteredCreditScore,
        hasCosigner: coSigner === 'Yes',
      });
      props.nextStep();
    } else {
      // Handle form validation errors
    }
  };

  const returnToPreviousStep = (e) => {
    e.preventDefault();
    props.prevStep();
};

  return (
    <div className={styles.formSection}>
      <h1>Financial Info</h1>
      <p className={styles.description}>Please provide the amount you are seeking, current education loans, and approximate FICO score.</p>
      <div className={styles.formElement}>
        <div className={styles.labelRow}>
          <label htmlFor="amount-seeking">Amount Seeking</label>
          {emptyAmountSeeking && <p className={styles.error}>Amount seeking cannot be empty.</p>}
          {invalidAmountSeeking && <p className={styles.error}>Please enter a valid amount.</p>}
        </div>
        <input
          className={
            emptyAmountSeeking || invalidAmountSeeking
              ? `${styles.yourInputInput} ${styles.inputError}`
              : `${styles.yourInputInput}`
          }
          type="text"
          id="amount-seeking"
          placeholder="e.g. $10,000"
          onChange={enteredAmountSeekingChangeHandler}
          onBlur={enteredAmountSeekingBlurHandler}
          value={enteredAmountSeeking}
        />
      </div>
      <div className={styles.formElement}>
        <div className={styles.labelRow}>
          <label htmlFor="current-education-loans">Current Education Loans</label>
          {emptyCurrentEducationLoans && <p className={styles.error}>Current education loans cannot be empty.</p>}
        </div>
        <select
          className={
            emptyCurrentEducationLoans ? `${styles.yourInputInput} ${styles.inputError}` : `${styles.yourInputInput}`
          }
          id="current-education-loans"
          onChange={enteredCurrentEducationLoansChangeHandler}
          onBlur={enteredCurrentEducationLoansBlurHandler}
          value={enteredCurrentEducationLoans}
        >
          <option value="">Select an option</option>
          {educationLoanCompanies.map((company) => (
            <option key={company} value={company}>
              {company}
            </option>
          ))}
        </select>
      </div>

      <div className={styles.formElement}>
        <div className={styles.labelRow}>
          <label htmlFor="credit-score">Credit Score</label>
          {emptyCreditScore && <p className={styles.error}>Credit score cannot be empty.</p>}
          {invalidCreditScore && <p className={styles.error}>Please enter a valid credit score.</p>}
        </div>
        <input
          className={
            emptyCreditScore || invalidCreditScore
              ? `${styles.yourInputInput} ${styles.inputError}`
              : `${styles.yourInputInput}`
        }
        type="text"
        id="credit-score"
        placeholder="e.g. 750"
        onChange={enteredCreditScoreChangeHandler}
        onBlur={enteredCreditScoreBlurHandler}
        value={enteredCreditScore}
      />

    </div>
    <div className={styles.formElement}>
      <div className={styles.labelRow}>
        <label htmlFor="co-signer">Do you have a co-signer?</label>
        {invalidCosigner && <p className={styles.error}>Please select an option.</p>}
      </div>
      <select
        className={invalidCosigner ? `${styles.yourInputInput} ${styles.inputError}` : `${styles.yourInputInput}`}
        id="co-signer"
        onChange={coSignerChangeHandler}
        onBlur={coSignerBlurHandler}
        value={coSigner}
      >
        {coSignerOptions.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
    </div>

      <div className={styles.buttonRow}>
      <PreviousStepButton onClick={returnToPreviousStep}>Go Back</PreviousStepButton>
        <NextStepButton onClick={continueToNextStep}>Next Step</NextStepButton>
      </div>
    </div>
  );
};

export default FinancialFormSection;
