import React, { useState } from 'react';

import YourInfoFormSection from './YourInfoFormSection';
import SelectPlanFormSection from './SelectPlanFormSection';
import AddonsFormSection from './AddonsFormSection';
import ConfirmationFormSection from './ConfirmationFormSection';
import ThankYouFormSection from './ThankYouFormSection';
import MoreInfoFormSection from './MoreInfoFormSection';
import EducationInfoSection from './EducationInfoSection';
import FinancialFormSection from './FinancialFormSection';
// import PaymentFormSection from './PaymentFormSection';
import emailjs from 'emailjs-com';

import firebase from 'firebase/compat/app';
import { initializeApp } from "firebase/app";
import 'firebase/compat/database';

// import { google } from 'googleapis';



import styles from './Form.module.css';
  

const Form = (props) => {

    const [formStep, setFormStep] = useState(1);

    const sendEmail = (formData) => {
        const templateParams = {
          to_email: formData.emailAddress,
          subject: 'No-reply: Thanks for your submission of the application to Spark by Delta Rating Inc.',
          body: `Dear ${formData.name},
          Thanks for your submission of the application. We are so glad that you took this step as a huge part in your journey to achieve your dreams. You can now sit back and relax, while we take it from here!
          
          Best of luck!
          Spark Team`
        };
      
        emailjs.send('service_c54im13', 'template_e38celb', templateParams, 'KgnoG4tSYi4Yc38Rd')
          .then((response) => {
            console.log('Email sent successfully!', response);
          })
          .catch((error) => {
            console.error('Failed to send email:', error);
          });
      };

    const [formData, setFormData] = useState({
        step: 1,
        name: props.name || '',
        emailAddress: props.emailAddress || '',
        phoneNumber: props.phoneNumber || '',
        ssn: props.ssn || '',
        plan: [
            {
                name: 'Free',
                desc: '\n> Guaranteed Review\n',
                selected: false,
                cost: 0,
            },
            {
                name: 'Regular',
                desc: '\n> Guaranteed Review & Bundling\n\n > Expedited processing\n',
                selected: true,
                cost: 100,
            },
            {
                name: 'Premium',
                desc: '\n> Guaranteed Review & Bundling\n\n > Expedited processing\n\n > Guaranteed Approval\n\n > Money back guarantee\n\n > Lowest rate', 
                selected: false,
                cost: 200,
            },
        ], 
        onlineService: true,
        largerStorage: false,
        customiseProfile: true,
        addons: [
            {
                id: 'transcript',
                name: 'Upload Transcripts',
                selected: true,
            },
        ],
    });

    const prevStep = () => {
        props.stepChange(formData.step - 1);
        setFormData((prevState) => {
            return {
                ...prevState,
                step: prevState.step - 1,
            };
        });
    };

    const nextStep = () => {
        props.stepChange(formData.step + 1);
        setFormData((prevState) => {
            return {
                ...prevState,
                step: prevState.step + 1,
            };
        });
    };

    const handleChange = (input) => {
        setFormData((prevState) => {
            return {
                ...prevState,
                ...input,
            };
        });
    };

   

    const handleFormChange = (formData) => {
        // Handle the form data (e.g., store it in state, send it to backend, etc.)
        console.log(formData);

        // Upload formData JSON to Realtime Database
        const firebaseConfig = {
            apiKey: "AIzaSyC75Kjsgf4x04ryBFDcLqOz6RbRUn0W3R4",
            authDomain: "delta-analytics-62c50.firebaseapp.com",
            databaseURL: "https://delta-analytics-62c50-default-rtdb.firebaseio.com",
            projectId: "delta-analytics-62c50",
            storageBucket: "delta-analytics-62c50.appspot.com",
            messagingSenderId: "804453122414",
            appId: "1:804453122414:web:69d0ebd0e47eca4473c17f",
            measurementId: "G-7ZDLD5ERNT"
        };

        firebase.initializeApp(firebaseConfig);

        const database = firebase.database();
        const formDataRef = database.ref('formData');

        formDataRef.push().set(formData, (error) => {
            if (error) {
            console.error('Error uploading formData:', error);
            } else {
            console.log('formData uploaded successfully to Firebase Realtime Database.');
            }
        });

        // uploadFileToDrive(formData);

        sendEmail();
      };
      

    const renderStep = () => {
        switch (formData.step) {
            case 1:
                return (
                    <YourInfoFormSection
                        nextStep={nextStep}
                        handleChange={handleChange}
                        values={{
                            name: formData.name,
                            emailAddress: formData.emailAddress,
                            phoneNumber: formData.phoneNumber,
                            ssn: formData.ssn,
                          }}
                    ></YourInfoFormSection>
                );

                case 2:
                return (
                    <EducationInfoSection
                    prevStep={prevStep}
                    nextStep={nextStep}
                    handleChange={handleChange}
                    values={{
                        fieldOfStudy: formData.fieldOfStudy,
                        levelOfStudy: formData.levelOfStudy,
                        universityName: formData.universityName, 
                      }}
                    ></EducationInfoSection>
                );

                case 3:
                return (
                    <MoreInfoFormSection
                    prevStep={prevStep}
                    nextStep={nextStep}
                    handleChange={handleChange}
                    values={{ 
                        GPA: formData.GPA,
                        graduationDate: formData.graduationDate,
                    }}
                    ></MoreInfoFormSection>
                );

                case 4:
                return (
                    <FinancialFormSection
                    prevStep={prevStep}
                    nextStep={nextStep}
                    handleChange={handleChange}
                    values={{ 
                        amountSeeking: formData.amountSeeking,
                        currentEducationLoans: formData.currentEducationLoans,
                        creditScore: formData.creditScore,
                        hasCosigner: formData.hasCosigner,
                    }}
                    ></FinancialFormSection>
                );

                case 5:
                    return (
                        <AddonsFormSection
                            prevStep={prevStep}
                            nextStep={nextStep}
                            handleChange={handleChange}
                            values={formData}
                        ></AddonsFormSection>
                    );
            case 6:
                return (
                    <SelectPlanFormSection
                        prevStep={prevStep}
                        nextStep={nextStep}
                        handleChange={handleChange}
                        values={formData}
                    ></SelectPlanFormSection>
                );
            
            case 7:
                return (
                    <ConfirmationFormSection
                        prevStep={prevStep}
                        nextStep={nextStep}
                        handleChange={handleChange}
                        values={formData}
                    ></ConfirmationFormSection>
                );
            case 8:
                return (
                    <ThankYouFormSection
                        prevStep={prevStep}
                        nextStep={nextStep}
                        handleChange={handleChange}
                        handleFormChange={handleFormChange} 
                        values={formData}
                    ></ThankYouFormSection>
                );
            default:
                return;
        }
    };

    console.log(formData);

    return <form >{renderStep()}</form>;
};

export default Form;
