import React, { useState } from 'react';
import styles from './AddonsFormSection.module.css';
import PreviousStepButton from '../UI/PreviousStepButton';
import NextStepButton from '../UI/NextStepButton';
import firebase from 'firebase/compat/app';
import { initializeApp } from "firebase/app";
import 'firebase/compat/database';
import 'firebase/compat/storage';
// import axios from 'axios';

const AddonsFormSection = (props) => {
  const firebaseConfig = {
    apiKey: "AIzaSyC75Kjsgf4x04ryBFDcLqOz6RbRUn0W3R4",
    authDomain: "delta-analytics-62c50.firebaseapp.com",
    databaseURL: "https://delta-analytics-62c50-default-rtdb.firebaseio.com",
    projectId: "delta-analytics-62c50",
    storageBucket: "delta-analytics-62c50.appspot.com",
    messagingSenderId: "804453122414",
    appId: "1:804453122414:web:69d0ebd0e47eca4473c17f",
    measurementId: "G-7ZDLD5ERNT"
  };

  firebase.initializeApp(firebaseConfig);
  const storage = firebase.storage();
  // const database = firebase.database();
  // const filesRef = database.ref('files');
  // const storageRef = storage.ref();


  const [transcriptFile, setTranscriptFile] = useState(null);

  const handleTranscriptUpload = (event) => {
    const file = event.target.files[0];
    if (file && file.type === 'application/pdf') {
      setTranscriptFile(file);
    } else {
      setTranscriptFile(null);
      console.log('Please select a valid PDF file for the transcript.');
    }
  };

  const uploadFile = (file) => {
    return new Promise((resolve, reject) => {
      const storageRef = storage.ref();
      const fileRef = storageRef.child(file.name);
      const uploadTask = fileRef.put(file);

      uploadTask.on(
        'state_changed',
        (snapshot) => {
          // Progress monitoring if needed
        },
        (error) => {
          reject(error);
        },
        () => {
          uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
            resolve(downloadURL);
          });


        }
      );
    });
  };

  // const handleUpload = async () => {
  //   if (transcriptFile) {
  //     try {
  //       const transcriptUrl = await uploadFile(transcriptFile);

  //       // Send the file URLs to the backend for storing in Firebase
  //       filesRef.push().set({
  //         transcriptUrl,
  //       });
        
  //       console.log('Files uploaded and URLs stored in Firebase Realtime database.');
  //       continueToNextStep();
  //     } catch (error) {
  //       console.error('Error uploading files:', error);
  //     }
  //   } else {
  //     console.log('Please select the right file');
  //   }
  // };

 

  const returnToPreviousStep = (e) => {
    e.preventDefault();
    props.prevStep();
  };

  const continueToNextStep = async(e) => {
    e.preventDefault();
    const transcriptUrl = await uploadFile(transcriptFile);
    props.handleChange({ downloadURL: transcriptUrl });
    props.nextStep();
  };

  return (
    <div className={styles.formSection}>
      <h1>Upload documents</h1>
      <p>Upload the required documents:</p>

      <br/><br/>

      <div className={styles.formElement}>
        <label htmlFor="transcriptFile">Transcript:</label>
        <br/><br/>
        <input
          
          type="file"
          id="transcriptFile"
          accept=".pdf"
          onChange={handleTranscriptUpload}
        />
      </div>

      <br/><br/>
      

      <div className={styles.buttonRow}>
        <PreviousStepButton onClick={returnToPreviousStep}>Go Back</PreviousStepButton>
        <NextStepButton onClick={continueToNextStep}>Next Step</NextStepButton>
        {/* <button onClick={}>Upload Files</button> */}
      </div>
    </div>
  );
};

export default AddonsFormSection;
