import React from 'react';

import PreviousStepButton from '../UI/PreviousStepButton';
import NextStepButton from '../UI/NextStepButton';
import AcceptTermsCheckbox from './AcceptTermsCheckbox';

import styles from './ConfirmationFormSection.module.css';

// import { useHistory } from 'react-router-dom';


const ConfirmationFormSection = (props) => {
    const {
        name,
        emailAddress,
        phoneNumber,
        planType,
        monthlyYearly,
        onlineService,
        largerStorage,
        customiseProfile,
        plan,
        addons,
    } = props.values;

    console.log('name', name);

    const returnToPreviousStep = (e) => {
        e.preventDefault();
        props.prevStep();
    };

    const continueToNextStep = async (e) => {
        e.preventDefault();
       // Redirect to another link
        const redirectUrl = 'https://google.com'; // Replace with your desired URL
        window.location.href = redirectUrl;

        // Assuming you're waiting for a confirmation HTTP response from the redirected page
        try {
            // Make an HTTP request to the server to check for confirmation
            const response = await fetch('/checkConfirmation'); // Replace with your endpoint for checking confirmation

            if (response.ok) {
            // Confirmation received, move to the next step
            props.nextStep();
            } else {
            // Confirmation not received, handle error or show appropriate message
            console.log('Confirmation not received. Handle error or show appropriate message.');
            }
        } catch (error) {
            // Error occurred while checking confirmation, handle it accordingly
            console.error('Error occurred while checking confirmation:', error);
        }
};

    console.log(plan);

    const selectedPlan = plan.filter((el) => {
        return el.selected === true;
    });

    // Calculates the total cost by adding each active addon to the cost of the original plan.
    const totalCost = addons.reduce((total, el) => {
        if (el.selected) {
            return (total += el.cost);
        } else {
            return total;
        }
    }, selectedPlan[0].cost);

    if (selectedPlan[0].name == "Free"){
        selectedPlan[0].cost = 0;
    }

    if (selectedPlan[0].name == "Regular"){
        selectedPlan[0].cost = 100;
    }

    if (selectedPlan[0].name == "Premium"){
        selectedPlan[0].cost = 200;
    }

    console.log(totalCost);

    return (
        
        <div className={styles.formSection}>
            <h1>Finishing up</h1>
            <p>Double-check everything looks OK before confirming.</p>
            <div className={styles.overview}>
                <div className={styles.overviewHeader}>
                    <h2
                        className={styles.planTitle}
                    >{`${selectedPlan[0].name}`}</h2>
                    <p className={styles.planCost}>+${selectedPlan[0].cost}</p>
                </div>
                {/* <div className={styles.addons}>
                    {addons.map((el, idx) => {
                        return (
                            <div key={idx} className={styles.addon}>
                                {el.selected && <p className={styles.addonName}>{el.name}</p>}
                            </div>
                        );
                    })}
                </div> */}
            </div>

             {/* Add AcceptTermsCheckbox component */}
            <AcceptTermsCheckbox />
            
            <div className={styles.totalRow}>
                <p className={styles.totalText}>Total</p>
                <p className={styles.totalValue}>+${selectedPlan[0].cost}</p>
            </div>
            <div className={styles.buttonRow}>
                <PreviousStepButton onClick={returnToPreviousStep}>Go Back</PreviousStepButton>
                <NextStepButton onClick={continueToNextStep}>Pay</NextStepButton>
            </div>
        </div>
    );
};

export default ConfirmationFormSection;
